import {Component, OnInit} from '@angular/core';
import {AuthService, FacebookLoginProvider, GoogleLoginProvider, SocialUser} from 'angularx-social-login';
import {Router} from '@angular/router';
import {AuthenticationService} from '../../business/service/Admin/Auth/authentication.service';
import {TokenStorageService} from '../core/token-storage.service';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {UserService} from '../../business/service/Admin/Auth/user.service';
import {User} from '../../business/domain/Admin/user';

@Component({
  selector: 'app-dashboard',
  templateUrl: 'login.component.html'
})
export class LoginComponent implements OnInit {

  private user: SocialUser;
  private name: string;
  private photoUrl: string;
  private email: string;
  private loggedIn: boolean;
  loginForm: FormGroup;
  item: User;
  submitted = false;
  message: any;
  loggedUser: any;

  constructor(private authService: AuthService, private authenticationService: AuthenticationService,
              private tokenStorage: TokenStorageService, private router: Router, private formBuilder: FormBuilder,
              private userService: UserService
  ) { }

  signInWithGoogle(): void {
    this.authService.signIn(GoogleLoginProvider.PROVIDER_ID).then((userData) => {
      this.user = userData;
      localStorage.setItem('userData', JSON.stringify(userData));
      if (userData !== null || userData !== undefined) {
        this.userService.saveStaff(this.user).subscribe(
          data => {
             console.log(data);
             this.message = data.message;
             this.loggedUser = data;
            localStorage.setItem('email', userData.email);
            localStorage.setItem('name', userData.name);
            localStorage.setItem('photoUrl', userData.photoUrl);
          }
          , error => {
            console.log(error);
          }
          , () => {
            if (this.message === 'Validation error?' && this.loggedUser.user.department !== null) {
              const value = {
                username: userData.email,
                password: 'Pass1234'
              };
              this.authenticate(value);
            } else {
              this.router.navigate(['/login/assign-department']);
            }
          }
        );
      }
    });
  }

  signInWithFB(): void {
    this.authService.signIn(FacebookLoginProvider.PROVIDER_ID);
  }

  signOut(): void {
    this.authService.signOut();
  }

  ngOnInit(): void {
    localStorage.clear();
    this.loginForm = this.formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required],
    });

    this.authService.authState.subscribe((user) => {
      this.user = user;
      this.loggedIn = (user != null);
    });
  }

  get f() { return this.loginForm.controls; }

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.loginForm.invalid) {
      return;
    }

    this.authenticate(this.loginForm.value);
  }

  onReset() {
    this.submitted = false;
    this.loginForm.reset();
  }

  authenticate(value) {
    console.log(value.username);
    this.authenticationService.authenticate(value.username, value.password).subscribe(
      data => {
        localStorage.setItem('userRoles', JSON.stringify(data.user.userRoles));
        localStorage.setItem('email', data.user.userName);
        localStorage.setItem('name', data.user.displayName);
        this.tokenStorage.saveToken(data.token);
        this.item = data;
        console.log('supposed to push**********');

      },
      error => {
      },
      () => {
        console.log('supposed to push');
        this.router.navigate(['/gene/home']);
      }
    );
  }
}
