import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {UserService} from '../../../business/service/Admin/Auth/user.service';
import {DepartmentService} from '../../../business/service/Admin/SystemConfig/department.service';
import {AuthenticationService} from '../../../business/service/Admin/Auth/authentication.service';
import {TokenStorageService} from '../../core/token-storage.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './assign-department.component.html'
})
export class AssignDepartmentComponent implements OnInit {
  assignDepartmentForm: FormGroup;
  departmentList: any[];
  item: any;
  constructor( private router: Router, private formBuilder: FormBuilder,
               private userService: UserService, private departmentService: DepartmentService,
               private tokenStorage: TokenStorageService,
               private authenticationService: AuthenticationService) { }

  ngOnInit() {
    this.createForm();
    this.getDepartments();
  }

  createForm() {
    this.assignDepartmentForm = this.formBuilder.group({
      department: ['', Validators.required],
    });
  }

  onSubmit() {
    this.userService.setDepartment(localStorage.getItem('email'), this.assignDepartmentForm.value.department.name).subscribe(
      data => {
        console.log(data);
      },
      error => {}
      ,
      () => {
        const value = {
          username: localStorage.getItem('email'),
          password: 'Pass1234'
        };
        this.authenticate(value);
      }
    );
  }

  getDepartments() {
    this.departmentService.getAll().subscribe(
      data => {
        this.departmentList = data;
        console.log(data);
      },
      error => {}
      ,
      () => {
      }
    );
  }

  authenticate(value) {
    this.authenticationService.authenticate(value.username, value.password).subscribe(
      data => {
        localStorage.setItem('userRoles', JSON.stringify(data.user.userRoles));
        localStorage.setItem('email', data.user.userName);
        localStorage.setItem('name', data.user.displayName);
        this.tokenStorage.saveToken(data.token);
        this.item = data;
      },
      error => {
      },
      () => {
        this.router.navigate(['/gene/home']);
      }
    );
  }
}
